import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as DealDetialsActions from "../../actions/dealDetailsActions";
import WriteUp from "./WriteUpPage";
import getParameterByName from "../common/getParameterByName";

class WriteUpContainer extends React.Component {
  componentDidMount() {
    this.props.actions.loadWriteUp(
      this.props.match.params.dealId
        ? this.props.match.params.dealId
        : getParameterByName("deal_id")
        ? getParameterByName("deal_id")
        : ""
    );
  }

  render() {
    return (
      <WriteUp
        writeUp={this.props.dealDetails.writeUp}
        noWriteUp={this.props.dealDetails.noWriteUp}
        loading={this.props.dealDetails.loading}
      />
    );
  }
}

function mapStateToProps(state) {
  return {
    dealDetails: state.dealDetails,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(DealDetialsActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(WriteUpContainer);
