import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Modal from "react-modal";
import sanitizeHtml from "sanitize-html";
import moment from "moment";

class WriteUpPage extends React.Component {
  getSubmission() {
    const dirtySubmission = this.props.writeUp.deal_write_up;
    const cleanSubmission = sanitizeHtml(dirtySubmission);
    if (this.props.writeUp.deal_write_up) {
      return { __html: cleanSubmission };
    } else return { __html: "" };
  }

  render() {
    return (
      <div>
        {this.props.writeUp && !this.props.noWriteUp && !this.props.loading ? (
          <div className="write-up">
            <div className="panel mui-panel">
              <div>
                <Row className="panel-heading">
                  <Col md="12">
                    <div className="timing">
                      {this.props.writeUp.req_date ? (
                        <div className="write-up-date">
                          <img
                            src="/images/calendar.png"
                            id="calendar"
                            alt="calendar"
                          />
                          <p>
                            {moment(
                              new Date(this.props.writeUp.req_date)
                            ).format("MM/DD/YYYY")}
                          </p>
                        </div>
                      ) : (
                        ""
                      )}
                      {this.props.writeUp.req_date ? (
                        <div className="write-up-time">
                          <img src="/images/clock.png" id="clock" alt="clock" />
                          <p>
                            {moment(
                              new Date(this.props.writeUp.req_date)
                            ).format("LT")}
                          </p>
                        </div>
                      ) : (
                        ""
                      )}
                    </div>
                  </Col>
                </Row>
                <Row>
                  <div>
                    <Col md="12" className="write-up-content">
                      <div dangerouslySetInnerHTML={this.getSubmission()} />
                    </Col>
                  </div>
                </Row>
              </div>
            </div>
          </div>
        ) : (
          ""
        )}
        <Modal
          className="loading-modal"
          isOpen={this.props.loading}
          ariaHideApp={false}
          contentLabel="Modal"
          shouldCloseOnOverlayClick={false}
        >
          <div className={this.props.loading ? "centered-spinner" : "hidden"}>
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
        </Modal>

        <div className={this.props.noWriteUp ? "" : "hidden"}>
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">No Write Up</h2>
          </div>
        </div>
      </div>
    );
  }
}

export default WriteUpPage;
